<template>
  <Transition name="yu-dialog">
    <div class="yu-dialog-wrap" v-show="show">
      <div class="yu-dialog-mask" @click="handleMask" @touchmove.stop.prevent="mousewheel" @mousewheel="mousewheel"></div>
      <div class="yu-dialog" :style="{width:width}">
        <div class="yu-dialog-title" v-show="title && title.length">{{ title }}</div>
        <div class="yu-dialog-content" :style="{ textAlign: align }">
          <div class="yu-dialog-desc" v-if="desc && desc.length" v-html="desc"></div>
          <slot v-else></slot>
        </div>
        <div class="yu-dialog-btn" v-if="showCancel || showConfirm">
          <div class="btn yu-dialog-cancel" @click="handleCancel" v-if="showCancel">{{ cancelText }}</div>
          <div class="btn yu-dialog-confirm" :style="{background:color}" @click="handleConfirm" v-if="showConfirm">{{ confirmText }}
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  name: 'Dialog',
  props: {
    show: {
      type: Boolean,
      default() {
        return false
      }
    },
    title: {
      type: String,
      default() {
        return ''
      }
    },
    desc: {
      type: String,
      default() {
        return ''
      }
    },
    showCancel: {
      type: Boolean,
      default() {
        return true
      }
    },
    cancelText: {
      type: String,
      default() {
        return '取消'
      }
    },
    showConfirm: {
      type: Boolean,
      default() {
        return true
      }
    },
    confirmText: {
      type: String,
      default() {
        return "确定"
      }
    },
    align: {
      type: String,
      default() {
        return "left"
      }
    },
    maskClose: {
      type: Boolean,
      default() {
        return true
      }
    },
    confirmClose: {
      type: Boolean,
      default() {
        return true
      }
    },
    showClose: {
      type: Boolean,
      default() {
        return false
      }
    },
    width: {
      type: String,
      default() {
        return 'auto'
      }
    },
    color: {
      type: String,
      default() {
        return ''
      }
    }
  },
  data() {
    return {}
  },
  methods: {
    mousewheel(e) {
      e.preventDefault();
    },
    handleMask() {
      this.maskClose && this.handleCancel();
    },
    handleCancel() {
      this.$emit("update:show", false);
      this.$emit('cancel') // 取消操作
    },
    handleConfirm() {
      if (this.confirmClose) {
        this.$emit("update:show", false);
      }
      this.$emit('confirm') // 确定操作
    }
  }
}
</script>
<style lang="scss" scoped>
.yu-dialog-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 990;

  .yu-dialog-mask {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #000;
    opacity: 0.6;
  }

  .yu-dialog {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    min-width: 400px;
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
    transform: translate(-50%, -50%);

    .yu-dialog-title {
      padding: 12px 15px;
      font-size: 18px;
      font-weight: bolder;
      text-align: left;
      line-height: 24px;
      word-wrap: break-word;
      word-break: break-all;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    .yu-dialog-content {
      font-size: 14px;
      line-height: 1.4;
      word-wrap: break-word;
      word-break: break-all;
      color: #808080;
      min-height: 40px;
      max-height: 600px;
      overflow-y: auto;

      .yu-dialog-desc {
        padding: 12px 15px;
      }
    }

    .yu-dialog-content-justify {
      text-align: justify;
    }

    .yu-dialog-content-left {
      text-align: left;
    }

    .yu-dialog-content-center {
      text-align: center;
    }

    .yu-dialog-content-right {
      text-align: right;
    }

    .yu-dialog-btn {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      padding: 10px 15px 15px;

      .btn {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        padding: 0 15px;
        cursor: pointer;
        border-radius: 3px;
        user-select: none;
        background: #3565ee;
        text-align: center;

        &:hover {
          opacity: 0.8;
          filter: alpha(opacity=80);
        }
      }

      .yu-dialog-confirm {
        background: #3565ee;
        color: #fff;
      }

      .yu-dialog-cancel {
        margin-right: 12px;
        background: #f2f2f2;
        color: #888;
      }
    }
  }
}

.yu-dialog-enter-active,.yu-dialog-leave-active {
  transition: all 1s ease-out;
}

//.yu-dialog-leave-active {
//  transition: all 1s ease;
//}

.yu-dialog-enter-from,
.yu-dialog-leave-to {
  transform: translateY(-100px);
  opacity: 0;
}

</style>
