<template>
  <YPage :logo="require('@/assets/img/logo_white.png')">
    <div class="s-tool-wrapper">
      <div class="yu-page-pc excel-wrap">
        <div class="excel-table-form">
          <table>
            <tr>
              <th>名称</th>
              <th class="inp">星级1</th>
              <th class="inp">星级2</th>
              <th class="inp">星级3</th>
              <th class="inp">楼层1</th>
              <th class="inp">楼层2</th>
              <th class="inp">楼层3</th>
              <th class="inp">基本</th>
              <th class="inp">全勤</th>
              <th>岗位基本</th>
