              <th>合计</th>
            </tr>
            <tr v-for="(item,index) in wordTableData" :key="index">
              <td v-text="item.name"></td>
              <td><input v-model="item.dormStar1" type="number" @change="bindChangeSum(index)"/></td>
              <td><input v-model="item.dormStar2" type="number" @change="bindChangeSum(index)" :disabled="!item.dormStar2"/></td>
              <td><input v-model="item.dormStar3" type="number" @change="bindChangeSum(index)" :disabled="!item.dormStar3"/></td>
