              <td><input v-model="item.dormFloor2" type="number" @change="bindChangeSum(index)" :disabled="!item.dormFloor2"/></td>
              <td><input v-model="item.dormFloor3" type="number" @change="bindChangeSum(index)" :disabled="!item.dormFloor3"/></td>
              <td><input v-model="item.basic" type="number" @change="bindChangeSum(index)"/></td>
              <td><input v-model="item.fullWork" type="number" @change="bindChangeSum(index)"/></td>
