        </div>
        <div class="yu-flex">
          <div class="excel-operate-wrap">
            <div class="excel-header-average yu-flex">
              <input class="form-input yu-flex-item" v-model="equation" placeholder="请输入算式计算平均数(格式:1+2+3+4)"/>
              <t-button @click="handleAverage">计算平均数</t-button>
            </div>
            <div class="excel-operate">
              <div class="excel-operate-hd">
                <div class="btn-wrap file-upload">
                  <input class="inp" type="file" id="fileUploader" @change="changeExcel" name="fileUploader" accept=".xls, .xlsx" placeholder=" "/>
                  <div class="btn btn-primary">点击上传</div>
                </div>
                <div class="btn-wrap file-upload">
                  <input class="inp" type="file" id="fileExcelUploader" @change="changeWord" name="fileExcelUploader" accept=".docx" placeholder=" "/>
                  <div class="btn btn-blue">上传word模板</div>
                </div>
                <div class="btn-wrap">
                  <div class="btn btn-green" @click="handleTableOne">表1</div>
                </div>
                <div class="btn-wrap">
                  <div class="btn btn-yellow" @click="handleTableTwo">表2</div>
                </div>
                <div class="btn-wrap">
                  <div class="btn btn-red" @click="handleExportExcel">导出excel数据</div>
                </div>
                <div class="btn-wrap">
                  <div class="btn" @click="handleClearDormData">清空存储</div>
                </div>
              </div>
              <div class="excel-operate-bd">
                <div class="average" v-show="average" v-text="'星级奖励平均：' + average"></div>
                <div v-for="(obj,index) in dormList" :key="index">
                  <h1 v-text="obj.key"></h1>
                  <div class="yu-flex">
                    <div style="flex:1.5;">
                      <h2>-----{{ obj.key }}表一数据-----</h2>
                      <p v-text="'总人数：' + obj.total"></p>
                      <p v-text="obj.threeStr"></p>
                      <p v-text="obj.fourStr"></p>
                      <p v-text="obj.fiveStr"></p>
                    </div>
                    <div style="width: 30px;height: 30px;"></div>
                    <div class="yu-flex-item">
                      <h2>-----{{ obj.key }}表二数据-----</h2>
                      <p>
                        <span v-text="obj.threeNumber"></span>
                        <span v-text="obj.fourNumber"></span>
                        <span v-text="obj.fiveNumber"></span>
                      </p>
                      <p v-text="obj.remark"></p>
                      <p v-text="obj.fourTotal"></p>
                      <p v-text="obj.fiveTotal"></p>
                      <p v-text="obj.totalStr"></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="excel-content-wrap">
            <div class="excel-table-form">
              <table>
                <tr>
                  <th>序号</th>
                  <th>姓名</th>
                  <th>五星级</th>
                  <th>四星级</th>
                  <th>三星级</th>
                  <th>楼层人数</th>
                  <th>等级</th>
                  <th>楼层岗位奖金额</th>
                  <th>备注</th>
                </tr>
                <tr v-for="(item,index) in excelTableData" :key="index">
                  <td v-text="item.key"></td>
                  <td v-text="item.name"></td>
                  <td v-text="item.five"></td>
                  <td v-text="item.four"></td>
                  <td v-text="item.three"></td>
                  <td v-text="item.people"></td>
                  <td v-text="item.degree"></td>
                  <td v-text="item.amount"></td>
                  <td v-text="item.remark"></td>
                </tr>
              </table>
            </div>
            <!-- 对象写法 JSON.stringify(value)-->
            <template v-if="excelData && excelData[current]">
              <div class="excel-content">
                <div class="excel-tab">
                  <span :class="{current:current === key}" @click="handleTab(key)" v-for="(value,key) in excelData" :key="key" v-text="key"></span>
                </div>
                <div class="excel-table">
                  <table>
                    <tr>
                      <th v-for="(value,key) in excelData[current][0]" :key="key" v-text="key"></th>
                    </tr>
                    <tr v-for="(item,index) in excelData[current]" :key="index">
                      <td v-for="(subItem,key) in item" :key="key" v-text="item[key]"></td>
                    </tr>
                  </table>
                </div>
              </div>
            </template>
            <div class="excel-content" v-else>
              <div class="excel-empty">请先上传数据</div>
            </div>
          </div>
        </div>
      </div>
      <div class="yu-page-app">
        <!--				<Empty text="请在电脑上使用该功能"/>-->
      </div>
      <Dialog :show.sync="showDialogOne" width="800px" title="表1数据" :desc="tableOne"/>
      <Dialog :show.sync="showDialogTwo" width="800px" title="表2数据" :desc="tableTwo"/>
    </div>
  </YPage>
</template>

<script>
import YPage from '@/components/YPage.vue'
import Dialog from "@/components/Dialog.vue";
import {Button as TButton} from 'tdesign-vue';
import * as XLSX from 'xlsx'

const mammoth = require("mammoth/mammoth.browser");
export default {
  components: {
    Dialog,
    YPage, TButton
  },
  data() {
    return {
      current: '',//当前切换的
      equationArr: [],
      equation: '',
      excelData: {},
      dormList: [],
      average: 0,
      jsonHead: { // 导出对应表格头部以及数据
        key: "房号",
        five: "五星级",
        four: "四星级",
        three: "三星级",
        people: "楼层人数",
        degree: "等级",
        amount: "楼层岗位奖金额",
        remark: "备注"
      },
      jsonHead2: { // 导出对应表格头部以及数据
        workBasic:"岗位基本工资",
        star: "星级奖励",
        floor: "楼层岗位奖励",
        onDuty: "深夜值班",
        sum: "合计"
      },
      wordTableData: [],
      excelTableData: [],
      wordStr: '',
      showDialogOne:false,
      showDialogTwo:false,
      tableOne:'',
      tableTwo:''
    }
  },
  mounted() {
    let data = localStorage.getItem('excelData');
    let wordModuleData = localStorage.getItem('wordStr')
    if(wordModuleData){
      this.wordStr = JSON.parse(wordModuleData);
    }
    if (data) {
      let val = JSON.parse(data);
      this.current = Object.keys(val)[0];
      this.excelData = val;
      this.getAllData();
    }
  },
  methods: {
    handleClearDormData(){
      localStorage.removeItem('excelData');
      localStorage.removeItem('wordStr')
      window.location.reload();
    },
    handleStatistics(key) {
      let arr = this.excelData[key]; //key是宿舍开头字母数字,例如C3

      let obj = {
        key: key,
        total: 0,
        threeStr: '',
        fourStr: '',
        fiveStr: '',
        remark: '',
        fourTotal: '',
        fiveTotal: '',
        totalStr: '',
        threeNumber: '',
        fiveNumber: '',
        fourNumber: '',
        totalAmount: 0,
        threePercent: '',
        fourPercent: '',
        fivePercent: ''
      };
      let lowRoom = [],
          middleRoom = [];
      let three = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          },
          four = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          },
          five = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          };
      let roomType = this.checkType(key);
      // console.log(arr)
      for (let item of arr) {
        let classes = item.classes.replace(/\s*/g, ""); //去除所有空白字符
        let info = classes.split(/:共|：共|人/g);
        let people = info[1],
            room = info[0];
        obj.total += parseInt(people); //计算总人数

        if (item.arange === '三') {
          three.room.push(room);
          three.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              three.high += 1;
            } else if (people <= 6) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              three.high += 1;
            } else if (people <= 6) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              three.high += 1;
            } else if (people <= 5) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          }
        } else if (item.arange === '四') {
          four.room.push(room);
          four.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              four.high += 1;
            } else if (people <= 6) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              four.high += 1;
            } else if (people <= 6) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              four.high += 1;
            } else if (people <= 5) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          }
        } else if (item.arange === '五') {
          five.room.push(room);
          five.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              five.high += 1;
            } else if (people <= 6) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              five.high += 1;
            } else if (people <= 6) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              five.high += 1;
            } else if (people <= 5) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          }
        }
      }

      let totalRoom = four.num + five.num + three.num;
      let threePercent = this.getPercent(three.num, totalRoom),
          fourPercent = this.getPercent(four.num, totalRoom),
          fivePercent = this.getPercent(five.num, totalRoom);

      if (three.num !== 0) {
        obj.threeStr = '三星级：（共' + three.num + '间，占' + threePercent + '%)：' + three.room.join('、');
        obj.threeNumber = '三星级人数：（' + three.high + '、' + three.middle + '、' + three.low + ')；';
      }

      obj.fourStr = '四星级：（共' + four.num + '间，占' + fourPercent + '%)：' + four.room.join('、') + '。';
      obj.fiveStr = '五星级：（共' + five.num + '间，占' + fivePercent + '%)：' + five.room.join('、') + '。';
      obj.threePercent = threePercent;
      obj.fourPercent = fourPercent;
      obj.fivePercent = fivePercent;


      let lowStr = '';
      let middleStr = '';
      if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
        lowStr = lowRoom.join('、') + '人数小于7人，按50%计算；';
        middleStr = '在7-9人之间';
      } else if (roomType === 'Gb') {
        lowStr = lowRoom.join('、') + '人数小于7人，按50%计算；';
        middleStr = '为7人间';
      } else if (roomType === 'Bc' || roomType === 'Gc') {
        lowStr = lowRoom.join('、') + '人数小于6人，按50%计算；';
        middleStr = '为6人间';
      }

      if (lowRoom.length > 0 && middleRoom.length > 0) {
        obj.remark = '备注：' + middleRoom.join('、') + '人数' + middleStr + '，按80%计算；' + lowStr;
      } else if (middleRoom.length > 0) {
        obj.remark = '备注：' + middleRoom.join('、') + '人数' + middleStr + '，按80%计算；';
      } else if (lowRoom.length > 0) {
        obj.remark = '备注：' + lowStr;
      } else {
        obj.remark = '备注：无'
      }
      // console.log(obj.remark)
      let threeTotal = three.high * 30 + three.middle * 30 * 0.8 + three.low * 30 * 0.5;
      let fourTotal = four.high * 50 + four.middle * 50 * 0.8 + four.low * 50 * 0.5;
      let fiveTotal = five.high * 60 + five.middle * 60 * 0.8 + five.low * 60 * 0.5;

      let three100 = three.high ? three.high + '*50' : '';
      let three80 = three.middle ? '+' + three.middle + '* 50 * 80%' : '';
      let three50 = three.low ? '+' + three.low + '* 50 * 50%' : '';

      let four100 = four.high ? four.high + '*50' : '';
      let four80 = four.middle ? '+' + four.middle + '* 50 * 80%' : '';
      let four50 = four.low ? '+' + four.low + '* 50 * 50%' : '';

      let five100 = five.high ? five.high + '*60' : '';
      let five80 = five.middle ? '+' + five.middle + '* 60 * 80%' : '';
      let five50 = five.low ? '+' + five.low + '* 60 * 50%' : '';

      let totalAmount = threeTotal + fourTotal + fiveTotal;
      if (threeTotal) {
        obj.threeNumber = '三星级人数：（' + three.high + '、' + three.middle + '、' + three.low + ')；';
        obj.threeTotal = '三星级（' + three100 + three80 + three50 + '=' + threeTotal + '元)';
      }
      if (fourTotal) {
        obj.fourNumber = '四星级人数：（' + four.high + '、' + four.middle + '、' + four.low + ')；';
        obj.fourTotal = '四星级（' + four100 + four80 + four50 + '=' + fourTotal + '元)';
      }
      if (fiveTotal) {
        obj.fiveNumber = '五星级人数：（' + five.high + '、' + five.middle + '、' + five.low + ')；';
        obj.fiveTotal = '五星级（' + five100 + five80 + five50 + '=' + fiveTotal + '元)';
      }

      let theTotalArr = [];
      if (threeTotal) {
        theTotalArr.push(threeTotal)
      }
      if (fourTotal) {
        theTotalArr.push(fourTotal)
      }
      if (fiveTotal) {
        theTotalArr.push(fiveTotal)
      }
      obj.totalStr = '共' + theTotalArr.join('+') + '=' + totalAmount + '元';

      obj.totalAmount = totalAmount;

      this.equationArr.push(totalAmount)

      // console.log(obj)
      this.dormList.push(obj);
    },
    //楼层岗位奖励
    floorReward(key, threePercent, fourPercent, fivePercent, total) {
      let roomType = this.checkType(key);
      let obj = {degree: '', amount: 0, remark: ''};
      if (roomType === 'Ba') {
        if (fivePercent >= 30 && !threePercent) {
          obj.degree = '一等奖';
          obj.amount += 1900;
        } else if (fivePercent >= 20 && !threePercent) {
          obj.degree = '二等奖';
          obj.amount += 1550;
        } else if ((fourPercent + fivePercent) >= 90 && threePercent < 10) {
          obj.degree = '三等奖';
          obj.amount += 1200;
        } else if (threePercent >= 10) {
          obj.degree = '常规奖';
          obj.amount += 950;
        }
      } else if (roomType === 'Bb' || roomType === 'Bc') {
        if (fivePercent >= 30 && !threePercent) {
          obj.degree = '一等奖';
          obj.amount += 950;
        } else if (fivePercent >= 20 && !threePercent) {
          obj.degree = '二等奖';
          obj.amount += 750;
        } else if ((fourPercent + fivePercent) >= 90 && threePercent < 10) {
          obj.degree = '三等奖';
          obj.amount += 550;
        } else if (threePercent >= 10) {
          obj.degree = '常规奖';
          obj.amount += 450;
        }
      } else if (roomType === 'Gb' || roomType === 'Gd') {
        if (fivePercent >= 60 && !threePercent) {
          obj.degree = '一等奖';
          obj.amount += 1900;
        } else if (fivePercent >= 40 && !threePercent) {
          obj.degree = '二等奖';
          obj.amount += 1550;
        } else if (fivePercent >= 20 && threePercent < 10) {
          obj.degree = '三等奖';
          obj.amount += 1200;
        } else if (fivePercent < 20) {
          obj.degree = '常规奖';
          obj.amount += 950;
        }
      } else if (roomType === 'Ga' || roomType === 'Gc') {
        if (fivePercent >= 60 && !threePercent) {
          obj.degree = '一等奖';
          obj.amount += 950;
        } else if (fivePercent >= 40 && !threePercent) {
          obj.degree = '二等奖';
          obj.amount += 750;
        } else if (fivePercent >= 20 && threePercent < 10) {
          obj.degree = '三等奖';
          obj.amount += 550;
        } else if (fivePercent < 20) {
          obj.degree = '常规奖';
          obj.amount += 450;
        }
      }
      if (roomType === 'Ba' || roomType === 'Gb' || roomType === 'Gd') {
        if (total > 250) {
          obj.amount += (total - 250) * 10;
          obj.remark = `人头多${total - 250}人`
        } else if (total < 200) {
          obj.amount -= (200 - total) * 10;
          obj.remark = `人头少${200 - total}人`
        }
      } else if (roomType === 'Bc' || roomType === 'Gc' || roomType === 'Bb' || roomType === 'Ga') {
        if (total > 120) {
          obj.amount += (total - 120) * 10;
          obj.remark = `人头多${total - 120}人`
        } else if (total < 80) {
          obj.amount -= (80 - total) * 10;
          obj.remark = `人头少${80 - total}人`
        }
      }
      return obj
    },
    //计算百分比
    getPercent(val, sum) {
      return Math.round(val / sum * 100)
    },
    //一次全部执行
    getAllData() {
      this.dormList = []; //数据初始化
      for (let key in this.excelData) {
        this.handleStatistics(key);
      }
      this.equation = this.equationArr.join('+'); //计算平均值
      this.createWordTable();
      this.createRewardExcelTable();
      this.handleAverage();
    },
    //上传excel数据
    changeExcel(e) {
      this.current = '';
      const files = e.target.files;
      if (files.length <= 0) {
        return false
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
        console.log('上传格式不正确，请上传xls或者xlsx格式')
        return false
      }
      let selectedFile = e.target.files[0];
      // 读取表格
      const fileReader = new FileReader()
      fileReader.onload = (event) => {
        let data = event.target.result;
        let workbook = XLSX.read(data, {
          type: 'binary'
        });
        let map = new Map();
        workbook.SheetNames.forEach(function (sheetName) {
          let excelData = XLSX.utils.sheet_to_row_object_array(workbook.Sheets[sheetName]);
          // 打印JSON数据
          if (excelData.length > 0) {
            map.set(sheetName, excelData)
          }
        })
        let allData = [...map]; //[...map]是一个二维数组

        let theObj = this.getGroup(allData[0][1]);
        console.log(theObj)
        this.excelData = theObj;
        localStorage.setItem('excelData', JSON.stringify(theObj))
        // setExcelObj(theObj) //本地存储
        this.getAllData();
      }
      fileReader.onerror = function (event) {
        console.error("上传excel文件出错,错误内容：" + event.target.error.code);
      };
      // 读取上传文件为二进制
      fileReader.readAsBinaryString(selectedFile);
    },
    //上传word模板
    changeWord(e) {
      const files = e.target.files;
      if (files.length <= 0) {
        return false
      } else if (!/\.(doc|docx)$/.test(files[0].name.toLowerCase())) {
        console.log('上传格式不正确，请上传doc或者docx格式')
        return false
      }
      let selectedFile = e.target.files[0];
      // 读取表格
      const fileReader = new FileReader()
      fileReader.onload = async (event) => {
        let data = event.target.result;
        const result = await mammoth.extractRawText({
          arrayBuffer: data
        });
        // console.log(result.value)
        if (!result.value) {
          console.log('请先上传word模板')
          return
        }
        console.log('上传成功')
        this.wordStr = result.value;
        localStorage.setItem('wordStr', JSON.stringify(result.value)) //储存本地
      }
      fileReader.onerror = function (event) {
        console.error("上传word文件出错,错误内容：" + event.target.error.code);
      };
      // 读取上传文件为二进制
      fileReader.readAsBinaryString(selectedFile);
    },
    //分组
    getGroup(data) {
      let obj = {};
      // console.log(data)
      data.forEach(item => {
        let info = item.classes.split(/：共|:共|人/g);
        let str = info[0].length >= 5 ? info[0].slice(0, 3) : info[0].slice(0, 2);
        let key = str.toUpperCase();
        if (!this.current) {
          this.current = key;
        }
        if (key && obj[key]) {
          obj[key].push(item)
        } else {
          obj[key] = obj[key] || [];
          obj[key].push(item)
        }
      });
      return obj;
    },
    //判断类型
    checkType(str) {
      str = str.toUpperCase();
      if (str === 'C3' || str === 'C4' || str === 'C5' || str === 'C6') {
        /*Ba类男生12人间*/
        return 'Ba'
      } else if (str === 'D2' || str === 'D3' || str === 'D4' || str === 'D5') {
        /*Bb类男生12人间D栋*/
        return 'Bb'
      } else if (str === 'D10' || str === 'D11' || str === 'D12') {
        /*Bc类男生8人间D栋*/
        return 'Bc'
      } else if (str === 'D6' || str === 'D7' || str === 'D8') {
        /*Ga类女生12人间D栋*/
        return 'Ga'
      } else if (str === 'B5' || str === 'B6' || str === 'B8') {
        /*Gb类女生10人间*/
        return 'Gb'
      } else if (str === 'D9' || str === 'D13' || str === 'D14') {
        /*Gc类女生8人间D栋*/
        return 'Gc'
      } else if (str === 'C8' || str === 'C9') {
        /*Gd类女生12人间C栋*/
        return 'Gd'
      }
    },
    // 切换tab
    handleTab(value) {
      this.current = value;
    },
    //排序1
    getGroupOrder(arr) {
      let cArr = [],
          bArr = [],
          dArr = [];
      for (let i = 0, len = arr.length; i < len; i++) {
        let keyStr = arr[i].key;
        if (keyStr[0].toUpperCase() === 'C') {
          cArr.push(arr[i])
        } else if (keyStr[0].toUpperCase() === 'B') {
          bArr.push(arr[i])
        } else if (keyStr[0].toUpperCase() === 'D') {
          dArr.push(arr[i])
        }
      }
      let cNewArr = this.getOrder(cArr);
      let bNewArr = this.getOrder(bArr);
      let dNewArr = this.getOrder(dArr);
      // console.log([...cNewArr, ...bNewArr, ...dNewArr])
      return [...cNewArr, ...bNewArr, ...dNewArr]
    },
    //排序2
    getOrder(arr) {
      return arr.sort((obj1, obj2) => {
        return obj1.key.slice(1, 3) - obj2.key.slice(1, 3)
      })
    },
    //计算平均数
    handleAverage() {
      let str = this.equation.replace(/\s*/g, ""); //去除所有空白字符
      let arr = str.split('+');
      let total = 0;
      for (let i = 0; i < arr.length; i++) {
        total += parseInt(arr[i]);
      }
      console.log(total)
      this.average = Math.round(total / 9)
    },
    //导出excel表格
    handleExportExcel() {
      const sheet1 = this.excelTableData.map(item => {
        const obj = {}
        for (const k in item) {
          if (this.jsonHead[k]) {
            obj[this.jsonHead[k]] = item[k]
          }
        }
        return obj
      })
      const sheet2 = this.wordTableData.map(item => {
        const obj = {}
        for (const k in item) {
          if (this.jsonHead2[k]) {
            obj[this.jsonHead2[k]] = item[k]
          }
        }
        return obj
      })
      // console.log(sheet1)
      console.log(sheet2)
      // 创建工作表
      const sheet1data = XLSX.utils.json_to_sheet(sheet1)
      const sheet2data = XLSX.utils.json_to_sheet(sheet2)
      // 创建工作簿
      const wb = XLSX.utils.book_new()
      // 将工作表放入工作簿中
      XLSX.utils.book_append_sheet(wb, sheet1data, 'sheet1')
      XLSX.utils.book_append_sheet(wb, sheet2data, 'sheet2')
      // 生成文件并下载
      XLSX.writeFile(wb, '教官星级宿舍奖励.xlsx')
    },
    //查看表1
    handleTableOne() {
      let str = this.wordStr;
      // console.log(str)
      if (!str) {
        console.log('请先上传word模板')
        return
      }
      this.showDialogOne = true
      this.creatTableOne(str)
    },
    //生成表1数据
    creatTableOne(wordData) {
      let arr = wordData.split('[插入]');
      let list = this.dormList;
      let text = '';
      for (let i = 0, leni = arr.length - 1; i < leni; i++) {
        let matchArr = arr[i].match(/([a-zA-Z])栋(\d+)楼/);
        let key = matchArr[1].toUpperCase() + matchArr[2];
        for (let j = 0, lenJ = list.length; j < lenJ; j++) {
          if (key === list[j].key) {
            text += arr[i] + '<br/><h1 style="color:#333;">' + list[j].threeStr + '<br/>' + list[j].fourStr + '<br/>' + list[j].fiveStr + '</h1><br/>';
            break;
          }
        }
      }
      this.tableOne = '<div style="color:#666;">' + text + '</div>';
    },
    //查看表2
    handleTableTwo() {
      let str = this.wordStr;
      if (!str) {
        console.log('请先上传word模板')
        return
      }
      this.showDialogTwo = true
      this.creatTableTwo(str)
    },
    //生成表2数据
    creatTableTwo(wordData) {
      let arr = wordData.split('[插入]');
      let list = this.dormList;
      console.log(arr)
      // console.log(list)
      let text = '';
      let totalStr = '';
      let totalAmount = 0;
      for (let i = 0, leni = arr.length - 1; i < leni; i++) {
        let matchArr = arr[i].match(/([a-zA-Z])栋(\d+)楼/);
        let key = matchArr[1].toUpperCase() + matchArr[2];
        for (let j = 0, lenJ = list.length; j < lenJ; j++) {
          if (key === list[j].key) {
            text += arr[i] + '<br/><h1 style="color:#333;">' + list[j].remark + '<br/>' + list[j].fourTotal +
                '<br/>' + list[j].fiveTotal + '<br/>' + list[j].totalStr + '</h1><br/>';
            totalAmount += list[j].totalAmount;
            if (i === (leni - 1)) {
              totalStr += list[j].totalAmount + '=' + Math.round(totalAmount / leni) + '元<br/>合计：' +
                  totalAmount + '元';
            } else {
              totalStr += list[j].totalAmount + '+';
            }
            break;
          }
        }
      }
      console.log(totalStr, totalAmount)
      this.tableTwo = '<div style="color:#666;">' + text + '<br/><b style="color:#333;">' + totalStr + '<b/></div>';
    },
