    //生成奖励表格
    createRewardExcelTable(){
      let exportData = [];666
      let val = JSON.parse(JSON.stringify(this.dormList)); //转为普通常规数据
      let newDormList = this.getGroupOrder(val);
      console.log(val)
      newDormList.forEach(item => {
        let {
          amount,
          degree,
          remark
        } = this.floorReward(item.key, item.threePercent, item.fourPercent, item.fivePercent, item.total)
        let name = this.getHousemasterName(item.key);
        exportData.push({
          key: item.key,
          name,
          five: item.fivePercent + '%',
          four: item.fourPercent + '%',
          three: item.threePercent ? item.threePercent + '%' : '',
          people: item.total,
          degree,
          amount,
          remark
        })
      })
      // console.log(exportData)
      this.excelTableData = exportData;
    },
    //计算属性-基本
    bindChangeSum(index){
      let obj = this.wordTableData[index];
      obj.workBasic = obj.basic;
      obj.star = obj.dormStar1;
      obj.floor = obj.dormFloor1;
      if(obj.fullWork){
        obj.workBasic = obj.basic + '+' + obj.fullWork;
      }
      if(obj.dormStar2 && !obj.dormStar3){
        obj.star = obj.dormStar1 + '+' + obj.dormStar2;
      }else if(obj.dormStar2 && obj.dormStar3){
        obj.star = obj.dormStar1 + '+' + obj.dormStar2 + '+' + obj.dormStar3;
      }
      if(obj.dormFloor2 && !obj.dormFloor3){
        obj.floor = obj.dormFloor1 + '+' + obj.dormFloor2;
      }else if(obj.dormFloor2 && obj.dormFloor3){
        obj.floor = obj.dormFloor1 + '+' + obj.dormFloor2 + '+' + obj.dormFloor3;
      }
      obj.sum = parseInt(obj.dormStar1) + parseInt(obj.dormStar2) + parseInt(obj.dormStar3) + parseInt(obj.dormFloor1) + parseInt(obj.dormFloor2) + parseInt(obj.dormFloor3) + parseInt(obj.basic) + parseInt(obj.fullWork)+ parseInt(obj.onDuty);
    },
    getHousemasterName(dorm){
      if(['C3','C4'].includes(dorm)){
        return '朱嘉俊'
      }else if(dorm === 'C6'){
        return '蓝世钊'
      }else if(['C8','C9'].includes(dorm)){
        return '王瑞霞'
      }else if(['D2','D3'].includes(dorm)){
        return '张小明'
      }else if(['D4','D5'].includes(dorm)){
        return '霍灿华'
      }else if(['D6','D7'].includes(dorm)){
        return '林美兰'
      }else if(['D8','D9'].includes(dorm)){
        return '张秋燕'
      }else if(['D10','D11','D12'].includes(dorm)){
        return '彭达盟'
      }else if(['D13','D14'].includes(dorm)){
        return '许素梅'
      }
    }
  }
}
